.card {
  background: var(--background-device-card);
  border-radius: 8px;
  box-shadow: var(--card-shadow);
  /* Тень */
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  cursor: pointer;
  width: 100%;
  /* Занимает всю доступную ширину */
  margin-bottom: 6px;
  /* Отступы между карточками */
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.card:hover {
  box-shadow: 0px 12px 32px rgba(0, 0, 0, 0.3);
  /* Увеличенная тень при наведении */
  transform: translateY(-2px);
  /* Эффект поднятия */
}

.card:active {
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.2);
  /* Уменьшенная тень при нажатии */
  transform: translateY(0);
  /* Возврат в исходное положение */
}

.card.selected {
  background-color: var(--background-header);
  /* Цвет выделенной карты */
}

.card.selected:hover {
  background-color: var(--background-header);
  /* Цвет выделенной карты при наведении */
}

.card-content {
  display: flex;
  justify-content: flex-start;
  min-height: 140px;
  height: 100%;
  /* Занимает всю высоту карточки */
}

.status-bar {

  width: 8px;
  border-radius: 8px 0px 0px 8px;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-grow: 1;
  /* Занимает оставшееся пространство */
}

.main-content {
  display: flex;
  padding-top: 5px;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer {
  display: flex;

  align-items: center;
}

.status-indicator {
  width: 15px;
  height: 15px;
  border-radius: 20px;
  margin-left: 10px;
}

.text {
  color: var(--text-color);
}

.device-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.additional-zone {
  color: #c0c0c0;
}

.signal-icon,
.battery-icon {
  margin: 0 3px;
}

.image-device {

  aspect-ratio: 1 / 1;
  /* Сохраняем квадратную форму */
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 90px;
  height: 90px;

}

.image-device img {

  height: 100%;
  object-fit: cover;
  /* Изображение заполняет контейнер, сохраняя пропорции */
}